    font-size: 20px;
  }
  .good .good__slider-btn {
    width: 30px;
    height: 30px;
  }
  .good .good__slider-btn.--prev {
    left: -45px;
  }
  .good .good__slider-btn.--next {
    right: -45px;
  }
}
@media screen and (max-width: 970px) {
  .about .about__benefits {
    display: grid;
