  font-family: "Apparat", sans-serif;
  font-weight: 400;
  font-style: normal;

  min-width: 0;
  min-height: 0;
}
html {
  overflow-x: hidden 
}
body {
  padding-right: 0 !important;
  color: #000;

}


html .--fw600 {
  font-weight: 600;
}
html .--fw500 {
  font-weight: 500;
}
.--uppercase {
  text-transform: uppercase;
}

.section {
  margin: 70px 0;
  &.--grey-bg {
    padding: 70px 0;
  }
}

