.title {
  .title__wrapper {

  }
  .title__slider {

  }
  .title__slide {
    position: relative;
    padding-bottom: 260px;
    padding-top: 392px;
  }
  .title__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
  }
  .title__desc {
    position: relative;
    z-index: 2;
  }
  .title__logo {
    max-width: 945px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 75px;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
  }
  .title__text {
    font-size: 57px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: -0.5644068121910095px;
    color: #626261;
  }
  .title__slider-bottom {
    position: absolute;
    bottom: 66px;
    left: 0;
    width: 100%;
    z-index: 10;
  }
  .title__slider-nav {

  }
}
body {
  .swiper-pagination-bullet {
    width: 20px;
    height: 20px;
    opacity: 1;
    background: #def3ff;
    margin: 0 !important;
    &:not(:last-child) {
      margin-right: 33px !important;
    }

    &.swiper-pagination-bullet-active {
      background: #8aceee;
    }
  }
}
.cloud-zone {
  background: #DEF3FF;
  background-blend-mode: multiply;
  position: relative;
  overflow: hidden;
}
.cloud {
  position: absolute;
  z-index: 0;
  pointer-events: none;


  &.--cloud-1 {
  right: -500px;
  top: -250px;
  }
  &.--cloud-2 {
    left: -500px;
    top: 190px;
  }
  &.--cloud-3 {
    right: -800px;
    top: 1800px;
  }
  &.--cloud-4 {
    left: -1500px;
    bottom: 800px;
  } 
}

.about {
  padding: 83px 0 71px;
  position: relative;
  z-index: 10;
  .about__wrapper {
    padding: 0 132px;
  }
  .about__header {

  }
  .about__text {
    font-size: 35px;
    font-weight: 300;
    line-height: 140%;
    letter-spacing: 0.4399999976158142px;
    text-align: center;
    color: #626261;
  }
  .about__benefits {
    margin-top: 45px;
    display: flex;
    justify-content: space-between;
    align-items: end;
  }
  .about__benefit {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .about__benefit-icon {
    object-fit: contain;
  }
  .about__benefit-desc {
    font-size: 29px;
    font-weight: 600;
    line-height: 90px;
    letter-spacing: 1.440000057220459px;
    text-align: center;
    color: #008cd3;
  }
}

.good {
  padding: 93px 0 0;
  position: relative;
  z-index: 1;
  &:last-child {
    padding-bottom: 90px;
  }
  .good__wrapper {
    padding: 0 132px;
  }
  .good__header {

  }
  .good__main-img {
    width: 100%;
    border-radius: 40px;
    overflow: hidden;
    img {
      width: 100%;
    }
  }
  .good__slider-wrapper {
    margin-top: 90px;
    position: relative;
  }
  .good__slider {

  }
  .good__slide {
    padding: 60px 60px 32px;
    background: #fff;
    border-radius: 40px;

    &.--mini {
      padding: 60px 32px 32px;
      .good__slide-name {
        font-size: 20px;
      }
      .good__slide-desc {
        font-size: 20px;
      }
    }
  }
  .good__slide-img {
    img {
      margin: 0 auto;
    }
  }
  .good__slide-name {
    font-size: 29px;
    font-weight: 400;
    line-height: 130%;
    letter-spacing: 1.440000057220459px;
    text-align: center;
    color: #008cd3;
    text-transform: uppercase;
    display: block;
  }
  .good__slide-desc {
    font-size: 25px;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: 1.440000057220459px;
    text-align: center;
    color: #008cd3;
    text-transform: lowercase;
  }
  .good__slider-nav {

  }
  .good__slider-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  .good__slider-btn {
    cursor: pointer;
    &.--prev {
      transform: scale(-1, 1) translateY(-50%);
      left: -135px;
    }
    &.--next {
      right: -135px;
    }
  }
}

@media screen and (max-width: 1440px) {
  .about .about__wrapper {
    padding: 0;
  }
}
@media screen and (max-width: 1220px) {
  .good .good__wrapper {
    padding: 0;
  }
  .title .title__text {
    font-size: 35px;
  }
  .title .title__slide {
    padding-top: 270px;
    padding-bottom: 150px;
    overflow: hidden;

  }
  .title .title__logo {
    max-width: 700px;
    left: -110px;
  }
  .about .about__text {
    font-size: 25px;
  }
  .about .about__benefit-desc {
    font-size: 20px;
  }
  .good .good__slider-btn {
    width: 30px;
    height: 30px;
  }
  .good .good__slider-btn.--prev {
    left: -45px;
  }
  .good .good__slider-btn.--next {
    right: -45px;
  }
}
@media screen and (max-width: 970px) {
  .about .about__benefits {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
  .about .about__benefit-icon {
    width: 90px;
    height: 90px;
  }
  .about .about__benefit-desc {
    font-size: 15px;
    line-height: 100%;
  }
  .about .about__text {
    font-size: 20px;
  }
  .good .good__slide-name {
    font-size: 17px;
  }
  .good .good__slide-desc {
    font-size: 15px;
  }
  .good .good__slide.--mini .good__slide-name {
    font-size: 15px;
  }
  .good .good__slide.--mini .good__slide-desc {
    font-size: 13px;
  }
  .good .good__wrapper {
    padding: 0 20px;
  }
  .good .good__slider-btn.--next {
    right: -30px;
  }
  .good .good__slider-btn.--prev {
    left: -30px;
  }
  .title .title__logo {
    left: -85px;
  }
  .title .title__slide {
    padding-top: 170px;
  }
  body .swiper-pagination-bullet:not(:last-child) {
    margin-right: 15px !important;
  }
}