.header {
    background: linear-gradient(0deg, #008CD3, #008CD3);
    padding: 62px 0;
    .header__wrapper {

   }
   .header__nav {

   }
   .header__nav-list {
    padding-left: 0;
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    margin: 0;
   }
   .header__nav-item {
    a {
        display: block;
    }
   }
}

@media screen and (max-width: 1220px) {
    .header {
        padding: 20px 0;
    }
    .header .header__nav-list {
        flex-wrap: wrap;
    }
    .header .header__nav-item {
        margin: 5px;
    }
}
@media screen and (max-width: 650px) { 
    .header .header__nav-item {
        &:nth-child(1) {
            width: 100%;
        }
        &:last-child {
            margin-left: auto;
        }
    }
}