.footer {
    padding: 89px 0 104px;
    background: linear-gradient(0deg, #008CD3, #008CD3);

   .footer__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: start;
    color: #fff;
   }
   .footer__text {
    font-size: 23px;
    font-weight: 400;
    line-height: 30px;
   }
   .footer__copy {
    max-width: 295px;
    width: 100%;
   }
   .footer__middle {

   }
   .footer__logo {
    margin-top: -33px;
    margin-bottom: 50px;
    display: block;
    img {

    }
   }
   .footer__terms {
    color: #fff;
    max-width: 417px;
    width: 100%;
    text-align: center;
    display: block;
   }
   .footer__links {

   }
   .footer__link {

   }
}
@media screen and (max-width: 1220px)  {
    .footer .footer__wrapper {
        flex-direction: column;
        align-items: center;
    }
    .footer__middle {
        order: 1;
        margin-bottom: 20px;
    }
    .footer__links {
        order: 3;
    }
    .footer__copy.footer__text {
        order: 2;
        text-align: center;
        margin-bottom: 20px;
    }
}
