@mixin link-btn {
  text-decoration: none;
  font-size: 26px;
  font-weight: 400;
  line-height: 100%;
  text-align: center;
  color: #fff;
  border: 1px solid #FFFFFF;
  border-radius: 68px;
  padding: 13px 35px;
  display: block;
  @media screen and (max-width: 768px) { 
    font-size: 18px;
    padding: 10px 25px;
  }
}
@mixin page-h2 {
  font-size: 48px;
  font-weight: 400;
  line-height: 100%;
  letter-spacing: -0.5644068121910095px;
  text-align: center;
  margin-bottom: 30px;
  margin-top: 0;
  color: #008cd3;
  @media screen and (max-width: 1220px) {
    font-size: 32px;
  }
}